import React from 'react'
import { stickerList } from '../misc/stickerList.js'
import { shuffleArray } from '../misc/common.js'
import { FlatButton } from '../components/button.jsx'
import ColorPicker from './ColorPicker.jsx'
import { closeModal } from '../actions'
import { connect } from 'react-redux'

class MyInfo extends React.Component {
  render = () => (
    <div className='modal'>
      <h1 className='caption animated bounceInDown'
        style={{ width: '90vw' }}>{localStorage.userName}</h1>
      <div className='caption animated bounceInDown'
        style={{ animationDelay: '0.2s' }}></div>
      <ColorPicker />
      <div className='caption animated bounceInDown' style={{ animationDelay: '0.3s' }}>
        <span className='fa fa-wechat' style={{ margin: '0 3px' }} />{this.props.myInfo.totalChatCount}
        <br />
        <span className='fa fa-smile-o' style={{ margin: '0 3px' }} />{this.props.myInfo.totalStickerCount}
        <br />
        <span className='fa fa-flash' style={{ margin: '0 3px' }} />{this.props.myInfo.totalEffectCount}
        <br />
        <span className='fa fa-shower' style={{ margin: '0 3px' }} />{this.props.myInfo.totalVoteCount}
        <br />
        <span className='fa fa-star' style={{ margin: '0 3px' }} />{this.props.myInfo.watchCount}
      </div>
      <br />
      <FlatButton onClick={this.props.closeModal}
        className="animated bounceInDown"
        style={{ animationDelay: '0.4s' }}
        minText="" icon="close" small={true} />
    </div>
  )
}
export default connect(
  ({ myInfo }) => ({
    myInfo: myInfo
  }),
  { closeModal }
)(MyInfo)